.footer {
	background: @fzcollight;
	&__navigation {
		display: flex;
		justify-content: space-between;
		padding: 100px 0px 100px 0px;
		&-column {
			text-align: left;
			color: @fzcol-1;
			.border_cont();
			// первая колонка
			&:first-child {
				max-width: 260px;
			}
			// вторая колонка
			&:nth-child(2) {
				min-width: 160px;
			}
			// третья колонка
			&:nth-child(3) {
				min-width: 160px;
			}
			// четвертая колонка
			&:nth-child(4) {
				min-width: 160px;
			}
			// пятая колонка
			&:last-child {
				max-width: 260px;
			}
			.logo {
				display: flex;
				align-items: center;
				max-width: 100%;
				&__icon {
					display: flex;
					align-items: center;
					.icon {
						margin: 0px 7px 0px 0px;
					}
					.toxin {
						margin: 0px 0px 0px 7px;
					}
				}
			}
				// .border_cont();
			
			.text {
				font-size: 1.4rem;
				line-height: 2.4rem;
				margin: 17px 0px 20px 0px;
			}
			
			
			.sub-title {
				font-size: 1.2rem;
				color: @fzcol-1;
				text-transform: uppercase;
				font-weight: 700;
				margin: 0px 0px 21px 0px;
			}
			.list {
				&__item {
					margin: 0px 0px 20px 0px;
					&:last-child {
						margin: 0px 0px 0px 0px;
					}
					&:hover {
						font-weight: 700;
					}
				}
				&__link {
					color: @fzcol-1;
				}
			}
			.form {
				display: flex;
				align-items: center;
				max-width: 100%;
				background: transparent;
				border: 1px solid rgba(31, 32, 65, 0.25);
				box-sizing: border-box;
				border-radius: 4px;
				padding: 0px 0px 0px 15px;
				&__input {
					flex: 1 1 auto;
					width: 100%;
					&::placeholder {
						font-size: 14px;
						line-height: 24px;
						color: @fzcol-1;
					}
				}
				._icon-arrow-right {
					display: flex;
					justify-content: center;
					align-items: center;
					font-size: 20px;
					color: @iconcolor;
					background: transparent;
					padding: 10px 15px 10px 15px;
					&:hover {
						color: darken(@iconcolor, 10%);
					}
				}
		}
		
		}
	}
	&__band {
		position: absolute;
		bottom: 70px;
		left: 0;
		right: 0;
		width: 100%;
		height: 2px;
		background: rgba(31, 32, 65, 0.1);
	}
	&__copirate {
		display: flex;
		align-items: center;
		justify-content: space-between;
		min-height: 70px;
		&-text {
			.copirate-text {
				color: @fzcol-1;
				font-size: 14px;
				line-height: 17px;
			}
		}
		&-icons {
			._icon-twitter {
				.icons-font();
			}
			._icon-facebook {
				.icons-font();
			}
			._icon-instagram {
				.icons-font();
				padding-right: 0;
			}
		}
	}
}